import React, { Component } from 'react'
import {BrowserRouter as Router,Redirect,Route,NavLink,Switch,privateRoute } from 'react-router-dom'
import {Button,TabBar} from 'antd-mobile';
import { NavBar,Icon,SearchBar, WhiteSpace, Carousel } from 'antd-mobile';
import Tabdetail from './Tabdetail';
import 'antd-mobile/dist/antd-mobile.css';

class Detail extends Component {
    render() {
        return (
            <div>
                <NavBar
                    icon={<Icon type="left"/>} mode="light"
                    onLeftClick={() => this.props.history.push('/')}
                >商品详情
                </NavBar>

                <img src={"https://gw.alicdn.com/bao/uploaded/i2/852062782/O1CN01WJ8Aq11WQCT1ulGRS_!!852062782-0-lubanu-s.jpg_500x500q90.jpg_.webp"} 
                style={{width:'100%'}} />

                <Router>
                    <Tabdetail />
                </Router>
          
		    </div>    
        )
    }
}

export default Detail;