import React from 'react';
import { NavBar,Icon,SearchBar,Carousel,TabBar } from 'antd-mobile';
import { BrowserRouter as Router, Route } from "react-router-dom";
import {withRouter} from 'react-router-dom';
import 'antd-mobile/dist/antd-mobile.css';


class Tabdetail extends React.Component {
	
	constructor(props) {
	  super(props);
	  this.state = {
		selectedTab: 'shop',
	  };
	}

	render() {
		const {pathname} =this.props.location;
	  	return (
			<div style={{ 
				position: 'fixed', width: '100%', bottom:0 
			}}>
				<TabBar
					unselectedTintColor="#949494"
					tintColor="red" 
					barTintColor="white" 
				>

				<TabBar.Item
					title="店铺"
					key="shop"
					icon={<i className='iconfont icon-dianpu'></i>}
					selectedIcon={<i className='iconfont icon-dianpu'></i>}					
					selected={pathname === '/shop'}
					onPress={() => {
						this.props.history.push('/shop')
					}
				}
				data-seed="logId"
				>
				</TabBar.Item>

				<TabBar.Item
					title="客服"
					key="worker"
					icon={<i className='iconfont icon-wode'></i>}
					selectedIcon={<i className='iconfont icon-wode'></i>}
					selected={pathname === '/worker'}
					onPress={() => {
						this.props.history.push('/worker')
					}
					}
					data-seed="logId1"
				>
				</TabBar.Item>

				<TabBar.Item
					title="收藏"
					key="collect"  
					icon={ <i className='iconfont icon-biaoqian'></i>}
					selectedIcon={<i className='iconfont icon-biaoqian'></i>}
					selected={pathname === '/collect'}
					onPress={() => {
						this.props.history.push('/collect')
					}}	
					data-seed="logId2"
				>
				</TabBar.Item>

				<TabBar.Item
					icon={<button
					style={{backgroundColor:'rgb(255, 152, 2)',color:'white',
					borderTopLeftRadius:'20px',borderBottomLeftRadius:'20px',
					border:'1px rgb(255, 152, 2) solid',height:'35px',width:'100px'}}>
						加入购物车
					</button>}
					key="addcart" 
				>
				</TabBar.Item>

				<TabBar.Item
					icon={<button className="right"
					style={{backgroundColor:'rgb(254, 92, 8)',color:'white',
					height:'35px',width:'100px',border:'1px rgb(254, 92, 8) solid',
					borderTopRightRadius:'20px',borderBottomRightRadius:'20px'}}>
						立即购买
					</button>}
					key="nowbuy"  
				>
				</TabBar.Item>
			</TabBar>
		</div>
	  );
	}
}

export default withRouter(Tabdetail);